.sidebar-container{
    width: 20vw;
    height: 100vh;
    background-color: white;
    border-right: 1px solid rgb(201, 199, 199);
}

.logo-container{
    height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 20px;
    border-bottom: 1px solid rgb(174, 172, 172);
}
.main-container{
    height: 35vh;
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.main-elements{
    height: 5vh;
    width: 18vw;
    display: flex;
    align-items: center;
    flex-direction: row;
}

.main-name-icons{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 14vw;
}

.main-greater-icons{
    transform: rotate(-90deg); 
}

.main-elements:hover {
    background-color: rgb(118, 118, 250);
    border-radius: 8px;
}

.secondary-container{
    height: 25vh;
    margin-left: 20px;
}
.extra-container{
    height: 15vh;
    margin-left: 20px;
}